.popup {
  border: 0px;
  position: absolute;
  width: 100%;
  height: 100%;
  margin: 0;
  z-index: 20;
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup:focus {
  outline: none;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 200;
  background-color: rgba(0, 0, 0, 0.75);
}

.backgroundPic {
  position: absolute;
  border: 0px;
  width: 600px;
  height: 90%;
  max-height: 800px;
  z-index: -10;
  box-shadow: 0px 5px 5px black;
}

.content {
  width: 500px;
  height: 80%;
  max-height: 600px;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.content>div {
  font-size: x-large;
}

.buttonsection {
  width: 100%;
  text-shadow: 2px 2px 2px gray;
  margin-top: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  gap: 20px;
}

.buttonsection>div {
  font-weight: bolder;
  transition: 500ms;
  margin-top: auto;
}

.buttonsection>div:hover {
  color: #db5c27;
  text-shadow: 0px 0px;
  cursor: pointer;
}

@media (max-width: 576px) {
  .backgroundPic {
    position: absolute;
    border: 0px;
    bottom: 10vh;
    width: 80%;
    height: 75%;
    z-index: -10;
  }
  .content {
    bottom: 10vh;
    width: 70%;
    height: 70%;
    display: flex;
    flex-direction: column;
    text-align: center;
  }
}