.guide {
  background-color: #fdda97;
  text-align: center;
  padding-top: 4vh;
  padding-left: 3vw;
  padding-right: 3vw;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  overflow: auto;
  color: #222422;
}

.moveGuide {
  opacity: 0.8;
  filter: alpha(opacity=40);
  width: 25vw;
  height: 15vw;
  margin: 0;
}

.eventIcon {
  width: 3.2vw;
  height: 3.2vw;
  margin-right: 1vw;
}

.eventPicture {
  width: 60vw;
  height: 30vw;
  margin-top: 1vw;
}

.mimiMapEventPicture {
  width: 20vw;
  height: 20vw;
}

.liHeaderContainer {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: center;
}

.eventDetail {
  margin: 0;
}

.bigHeader {
  font-size: 2vw;
  padding-left: 3vw;
  padding-right: 3vw;
}

.smallHeader {
  font-size: 2vw;
  color: #3e423e;
  margin: 0;
}

.plainText {
  font-size: 1.5vw;
  font-weight: bold;
}

.list {
  text-align: start;
  font-size: 1.5vw;
  font-weight: bold;
  color: #2a2e2a;
}

.liHeader {
  line-height: 5vw;
  padding-top: 0;
  padding-bottom: 0;
  margin: 0;
  font-weight: bolder;
}

.liHeaderText {
  font-weight: 900;
  color: #222422;
}

.list>li {
  margin-bottom: 0.2vh;
}

.gameModeName {
  font-weight: bold;
}

.description {
  margin: 0;
  text-align: start;
  padding-left: 10vw;
  padding-right: 10vw;
}

.topGoBackButton {
  align-self: flex-start;
}

.bottomGoBackButton {
  margin-top: 3vh;
  padding-bottom: 2.5vh;
}

.goBackButton:hover {
  font-size: 2.2vw;
  color: #db5c27;
}

.goBackButton {
  font-size: 2vw;
  font-weight: bolder;
  cursor: pointer;
  transition: 400ms;
}

@media (max-width: 576px) {
  .moveGuide {
    width: 60vw;
    height: 42vw;
  }
  .eventIcon {
    width: 4vw;
    height: 4vw;
    margin-right: 1vw;
  }
  .eventPicture {
    width: 50vw;
  }
  .eventDetail {
    margin: 0;
  }
  .bigHeader {
    font-size: 3vw;
    padding-left: 3vw;
    padding-right: 3vw;
  }
  .smallHeader {
    font-size: 3vw;
  }
  .plainText {
    font-size: 2vw;
    font-weight: bold;
    margin: 0;
  }
  .list {
    text-align: start;
    font-size: 2vw;
  }
  .list>li {
    margin-bottom: 2vh;
  }
  .goBackButton:hover {
    font-size: 4vw;
  }
  .goBackButton {
    font-size: 3vw;
  }
}